export const subsets = (array) => {
  if (array.length === 0) {
    return [[]];
  }

  const first = array[0];
  const withoutFirst = subsets(array.slice(1));
  // remember, we don't want to mutate the subsets without the first element
  // hence, the 'concat'
  const withFirst = withoutFirst.map(sub => [first].concat(sub) );

  return withoutFirst.concat(withFirst);
};

export const bsArray = (array, target) => {
  if (array.length === 0) {
    return null;
  }

  let midpoint = Math.floor(array.length / 2);
  if (array[midpoint] === target) {
    return midpoint;
  }

  if (target < array[midpoint]) {
    return bsArray(array.slice(0, midpoint), target);
  }

  if (target > array[midpoint]) {
    let value = bsArray(array.slice(midpoint + 1), target);
    if (value === null) { return null; }
    return value + midpoint + 1;
  }
};

export const shuffle = (array) => {
  let m = array.length;

  for (let i = m - 1; i >= 0; i--) {
    let random = Math.floor(Math.random() * i);
    [array[i], array[random]] = [array[random], array[i]];
  }
  return array;
};

export const flatten = (array) => {
  let result = [];
  array.forEach((el) => {
    if (Array.isArray(el)) {
      result = result.concat(flatten(el));
    } else {
      result.push(el);
    }
  });
  return result;
};

export const deepDup = (array) => {
  let result = [];
  array.forEach((el) => {
    if (Array.isArray(el)) {
      result.push(deepDup(el));
    } else {
      result.push(el);
    }
  });
  return result;
};

