export const waterBlocks = (blocks) => {
  let a = 0;
  let b = blocks.length - 1;
  let max = 0;
  let leftMax = 0;
  let rightMax = 0;
  while (a <= b) {
    leftMax = Math.max(leftMax, blocks[a]);
    rightMax = Math.max(rightMax, blocks[b]);
    if (leftMax < rightMax) {
      max += (leftMax - blocks[a++]);
    } else {
      max += (rightMax - blocks[b--]);
    }
  }
  return max;
};



